@import './_mixin.scss';
@import './_svg.scss';
@import './_variables.scss';

#root #wrap .content {
	// ============================== 홈 시작 ==============================
	&.main {
		grid-template-areas:
			'main-slide main-slide visual-text'
			'info rooms-tit rooms-tit'
			'info rooms-figure rooms-text'
			'info around faq'
			'news news news';
		.main-slide {
			grid-area: main-slide;
		}
		.visual-text {
			grid-area: visual-text;
			margin-bottom: 2rem;
			.sub-title {
				@include text-ellipsis(3);
				word-break: break-all;
			}
			.btn-viewall {
				display: block;
				margin-top: 1.2rem;
				text-align: center;
				.border-inner {
					padding: 2.8rem 0;
				}
				.pb {
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: left;
					margin-bottom: 0.8rem;
					gap: 0.4rem;
					.logo {
						height: 3.6rem;
						width: 3.6rem;
					}
					div {
						p {
							font-family: 'ChosunSm', serif;
						}
					}
				}
				span {
					font-family: 'ChosunSm', serif;
					font-size: 1.8rem;
				}
			}
			.progress {
				margin-bottom: 2rem;
			}
			@include mobile {
				.sub-title {
					-webkit-line-clamp: 2;
				}
			}
		}

		.rooms-tit {
			grid-area: rooms-tit;
			.tab-title {
				display: flex;
				gap: 1rem;
				align-items: flex-end;
				flex-wrap: wrap;
				height: 2.8rem;
				button {
					color: #999;
					transition-duration: 0.5s;
					font-size: 2rem;
					&.active {
						color: #000;
						font-size: 2.4rem;
					}
					span {
						color: inherit;
						font-size: 1.8rem;
						transition-duration: 0.5s;
					}
				}
			}
			@include mobile {
				.tab-title {
					height: 5.6rem;
					gap: 0;
					align-items: center;
					button {
						flex-basis: 50%;
						font-size: 1.2rem;
						height: 2.8rem;
						span {
							font-size: 1.6rem;
						}
						&.active {
							font-size: 1.4rem;
							span {
								font-size: 2rem;
							}
						}
					}
				}
			}
		}
		.rooms-figure {
			grid-area: rooms-figure;
			position: relative;
			.auto-height-figure {
				position: absolute;
				left: 0;
				top: 0;
				opacity: 0;
				width: 100%;
				transition-duration: 0.5s;
				&.active {
					opacity: 1;
				}
			}
			@include mobile {
				aspect-ratio: 16/9;
				margin-top: 1.2rem;
			}
		}
		.rooms-text {
			grid-area: rooms-text;
			position: relative;
			margin-bottom: 2rem;
			> div {
				margin-top: 1.2rem;
				&:first-child {
					margin-top: 0;
				}
				h3 + .text {
					height: 10rem;
				}
				.text {
					margin-top: 0.8rem;
				}
			}
			.control {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 2rem;
				border: 0.6rem double $default;
				font-family: 'ChosunLo', sans-serif;
				font-weight: 500;
				button {
					width: 2rem;
					height: 2rem;
					font-family: 'ChosunLo', sans-serif;
					font-weight: 500;
				}
			}
			@include uxga {
				> div h3 + .text {
					-webkit-line-clamp: 5;
					height: auto;
				}
			}
			@include mobile {
				margin-top: 1.2rem;
				.control {
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					margin-top: 0;
					padding: 1.6rem;
				}
				> div:first-child {
					padding-top: 7.6rem;
				}
			}
		}

		.info {
			grid-area: info;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			gap: 2.4rem;
			.info-reservation {
			}
			.info-waytocome {
				img {
					margin-bottom: 1.2rem;
				}
			}
		}

		.around {
			grid-area: around;
			&-list {
				margin-top: 1.2rem;
				display: flex;
				gap: 1.2rem;
				> li {
					flex: 1;
					.around-text {
						margin-top: 0.4rem;
						h4 {
							@include text-ellipsis(1);
							margin-bottom: 0.4rem;
						}
						.source {
							color: #666;
							text-align: right;
							margin: 0.2rem;
							position: relative;
							font-size: 1.4rem;
						}
						.text {
							@include text-ellipsis(2);
						}
					}
				}
			}
		}

		.faq {
			grid-area: faq;
			h4 {
				word-break: break-all;
			}
		}

		.news {
			grid-area: news;
			display: flex;
			flex-wrap: wrap;
			gap: 4rem;
			padding-top: 2.4rem;
			> button {
				position: relative;
				flex: 1;
				display: flex;
				gap: 1rem;
				text-align: left;
				&:not(:last-child)::after {
					content: '';
					display: block;
					width: 0.2rem;
					height: 110%;
					position: absolute;
					right: -2rem;
					top: -5%;
					background-color: #000;
				}
				.thumb {
					width: 20rem;
					img {
						aspect-ratio: 1/1;
						object-fit: cover;
					}
				}
				.cont {
					flex: 1;
					height: 100%;
					display: flex;
					flex-direction: column;
					justify-content: space-evenly;
					.tit {
						@include text-ellipsis(2);
					}
					.text {
						font-size: 1.4rem;
						@include text-ellipsis(5);
					}
				}
			}
			@include tablet {
				> button {
					flex-direction: column;
					.thumb {
						width: 100%;
						img {
							aspect-ratio: 16/9;
						}
					}
					&:nth-child(2)::after,
					&:last-child {
						display: none;
					}
				}
			}
			@include mobile {
				> button {
					.text {
						margin-top: 0.4rem;
					}
				}
			}
		}
	}
	// ============================== 홈  끝  ==============================

	// ============================== 펜션소개 시작 ==============================
	&.about {
		grid-template-areas:
			'gallery gallery gallery'
			'visual visual visual'
			'waytocome waytocome facilities';

		.gallery {
			grid-area: gallery;
			position: relative;
			.btn-origin {
				display: block;
				position: absolute;
				right: 1.2rem;
				top: 1.2rem;
				z-index: 9;
				width: 12rem;
				line-height: 6rem;
				border: 0.6rem double $default;
				background: #f3f3f3;
				outline: 0.6rem solid #f3f3f3;
				font-family: 'ChosunSm', serif;
				opacity: 0.7;
				text-align: center;
				&::after {
					content: '';
					user-select: none;
					pointer-events: none;
					display: block;
					width: calc(100% + 2.4rem);
					height: calc(100% + 2.4rem);
					position: absolute;
					left: -1.2rem;
					top: -1.2rem;
					background: url(../img/paper.png) 0 0/ 100% auto repeat-y;
				}
				@include btn-hover;
			}
			&-images {
				height: calc(100% - 202px);
				img {
					aspect-ratio: 7/3;
					object-fit: cover;
				}
				.swiper-button-prev,
				.swiper-button-next {
					bottom: calc(50% - 14px);
				}
			}
			&-thumbs {
				position: relative;
				height: 190px;
				margin-top: 12px;
				img {
					height: 100%;
					object-fit: cover;
				}
			}
			@include uxga {
				&-images {
					height: calc(100% - 185px);
				}
				&-thumbs {
					height: 165px;
				}
			}
			@include tablet {
				.btn-origin {
					width: 10rem;
					line-height: 4rem;
					outline: 0.4rem solid #f3f3f3;
					&::after {
						width: calc(100% + 2rem);
						height: calc(100% + 2rem);
						left: -1rem;
						top: -1rem;
					}
				}
				&-images {
					height: calc(100% - 122px);
				}
				&-thumbs {
					height: 110px;
				}
			}
			@include mobile {
				.btn-origin {
					width: 7.2rem;
					line-height: 2.4rem;
					font-size: 1.4rem;
				}
				&-images {
					height: calc(100% - 60px);
				}
				&-thumbs {
					height: 48px;
				}
			}
			@media (max-width: 420px) {
				&-images {
					height: calc(100% - 44px);
				}
				&-thumbs {
					height: 32px;
				}
			}
		}

		.visual {
			grid-area: visual;
			text-align: center;
			padding: 6rem 0 3.6rem 0;
		}

		.waytocome {
			grid-area: waytocome;
			.sub-title {
				margin-top: 1.2rem;
			}
		}

		.facilities {
			grid-area: facilities;
			.facility-list {
				display: grid;
				height: calc(100% - 50px);
				grid-template-columns: repeat(2, 1fr);
				text-align: center;
				align-items: center;
				align-content: stretch;
				li {
					justify-self: center;
					i {
						display: block;
						margin: 0 auto;
						width: 40px;
						height: 40px;
						border: 2px solid #000;
						border-radius: 50%;
						margin-bottom: 0.8rem;
					}
					p {
						font-size: 1.4rem;
					}
				}
			}
		}
	}
	// ============================== 펜션소개  끝  ==============================

	// ============================== 객실소개 시작 ==============================
	&.rooms {
		grid-template-columns: repeat(4, 1fr);
		.detail {
			h2 {
				text-align: left;
				.h3 {
					color: #666;
				}
			}
			button {
				.roomData-figure {
					position: relative;
					overflow: hidden;
					img {
						transition-duration: 0.5s;
					}
					img:nth-child(2) {
						position: absolute;
						left: 100%;
						top: 0;
					}
				}
				@include hoverable {
					&:hover .roomData-figure {
						img {
							transform: translateX(-100%);
						}
					}
				}
				figcaption {
					text-align: left;
				}
				.cont {
					margin-top: 1.2rem;
					p {
						margin-top: 0.4rem;
					}
				}
			}
		}
		.map {
			grid-column: 1 /5;
			position: relative;
			.map-figure {
				position: relative;
				i.img {
					display: block;
					aspect-ratio: 16/9;
					background: url(../img/overview.jpg) center / cover no-repeat;
				}
			}
			.btn-modal button,
			.info-content button i,
			.btn-modal div,
			.info-content div i {
				display: block;
				width: 26px;
				line-height: 26px;
				text-align: center;
				border-radius: 50%;
				font-size: 1.5rem;
				font-family: 'ChosunLo', sans-serif;
				@include hoverable {
					&:not(div):hover {
						&:not(button i)::before,
						&:not(button i)::after {
							content: '';
							display: block;
							width: 30px;
							height: 30px;
							position: absolute;
							left: calc(50% - 15px);
							top: calc(50% - 15px);
							border: 1px solid #fff;
							border-radius: 50%;
							box-sizing: border-box;
							opacity: 0;
							@keyframes hover {
								from {
									transform: scale(1);
									opacity: 0;
								}
								10% {
									opacity: 1;
								}
								to {
									transform: scale(2);
									opacity: 0;
								}
							}
						}
						&:not(button i)::before {
							animation: hover 1s linear 0s infinite;
						}
						&:not(button i)::after {
							animation: hover 1s linear 0.3s infinite;
						}
					}
				}
				&[class^='s'] {
					background-color: #a9e3ee;
				}
				&[class^='c'] {
					background-color: #dddddd;
				}
				&[class^='m'] {
					background-color: #ff7f80;
				}
				&[class^='v'] {
					background-color: #b0c4e9;
				}
				&.ex {
					background-color: #000;
					color: #fff;
				}
			}
			.info-list {
				position: absolute;
				left: 2rem;
				bottom: 4rem;
				width: 26rem;
				padding: 0.8rem;
				background-color: #ffffffd0;
				z-index: 1;
				display: flex;
				flex-direction: column;
				gap: 0.8rem;
				.info {
					&-title {
						font-size: 1.6rem;
						padding: 0.6rem;
						border-radius: 1.55rem;
						background: rgba($default, 0.7);
						text-align: center;
						color: #fff;
						margin-bottom: 0.4rem;
						strong {
							font-family: 'ChosunKm', serif;
							&.vally {
								color: #b0c4e9;
							}
							&.sky {
								color: #a9e3ee;
							}
							&.mountain {
								color: #ff7f80;
							}
							&.cloud {
								color: #dddddd;
							}
						}
						&:first-child {
							margin-top: 0;
						}
					}
					&-content {
						display: flex;
						flex-wrap: wrap;
						> * {
							display: flex;
							flex-basis: 50%;
							align-items: center;
							gap: 0.4rem;
							margin-top: 0.4rem;
						}
					}
				}
			}
			.btn-modal {
				button,
				div {
					position: absolute;
					transition-duration: 0.5s;
					&.s1 {
						left: 30%;
						top: 65%;
					}
					&.c1 {
						left: 33.5%;
						top: 63.5%;
					}
					&.c2 {
						left: 37%;
						top: 62%;
					}
					&.s2 {
						left: 40.5%;
						top: 60.5%;
					}

					&.v1 {
						left: 31.75%;
						top: 70%;
					}
					&.v2 {
						left: 35.25%;
						top: 68.5%;
					}
					&.m {
						left: 38.75%;
						top: 67%;
					}

					&.s3 {
						left: 57%;
						top: 47.5%;
					}
					&.c3 {
						left: 63.33%;
						top: 44.16%;
					}
					&.c4 {
						left: 69.66%;
						top: 40.83%;
					}
					&.s4 {
						left: 76%;
						top: 37.5%;
					}

					&.p1 {
						left: 27%;
						top: 69%;
					}
					&.p2 {
						left: 47%;
						top: 66%;
					}
					&.t {
						left: 51.5%;
						top: 64.5%;
					}
					&.o {
						left: 62%;
						top: 58%;
					}
					&.p3 {
						left: 92%;
						top: 41%;
					}
				}
			}
		}
		@include tablet {
			grid-template-columns: repeat(2, 1fr);
			.map {
				grid-column: 1 /3;
				.map-figure i.img {
					background-image: url(../img/overview_tablet.jpg);
				}
				.info-list {
					flex-direction: row;
					flex-wrap: wrap;
					position: relative;
					left: 0;
					top: 0;
					width: 100%;
					background: none;
					padding: 0;
					margin-bottom: 1.2rem;
					.info {
						flex: 1;
						flex-basis: 24rem;
						&-title {
							background: rgba($default, 0.6);
						}
						&-content button {
							pointer-events: none;
						}
					}
				}
				.btn-modal {
					button,
					div {
						&.s1 {
							left: 10%;
							top: 70%;
						}
						&.c1 {
							left: 13.5%;
							top: 68.5%;
						}
						&.c2 {
							left: 17%;
							top: 67%;
						}
						&.s2 {
							left: 20.5%;
							top: 65.5%;
						}

						&.v1 {
							left: 11.75%;
							top: 76%;
						}
						&.v2 {
							left: 15.25%;
							top: 74.5%;
						}
						&.m {
							left: 18.75%;
							top: 73%;
						}

						&.s3 {
							left: 48%;
							top: 45%;
						}
						&.c3 {
							left: 54.33%;
							top: 41.33%;
						}
						&.c4 {
							left: 60.66%;
							top: 37.66%;
						}
						&.s4 {
							left: 67%;
							top: 34%;
						}

						&.p1 {
							left: 7%;
							top: 75%;
						}
						&.p2 {
							left: 30%;
							top: 73%;
						}
						&.t {
							left: 36.5%;
							top: 70.5%;
						}
						&.o {
							left: 49%;
							top: 59%;
						}
						&.p3 {
							left: 90%;
							top: 38%;
						}
					}
				}
			}
		}
		@include mobile {
			.map {
				.map-figure i.img {
					background-image: url(../img/overview_mobile.jpg);
				}
				.btn-modal div,
				.btn-modal button,
				.sub-title {
					display: none;
				}
			}
		}
	}
	// ============================== 객실소개  끝  ==============================

	// ============================== 펜션소식 시작 ==============================
	&.news {
		grid-template-columns: repeat(4, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(2);
				height: 56px;
			}
			.date {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
				&::before {
					content: '';
					display: block;
					width: calc(100% - 7rem);
					height: 1px;
					border-top: 1px dashed #000;
					position: absolute;
					left: 0;
					top: calc(50% - 0.05rem);
				}
			}
			.text {
				img {
					display: none;
				}
				@include text-ellipsis(5);
			}
		}
		@include tablet {
			grid-template-columns: repeat(3, 1fr);
		}
		@include mobile {
			button {
				h3 {
					height: auto;
				}
			}
		}
	}
	// ============================== 펜션소식  끝  ==============================

	// ============================== 예약하기 시작 ==============================
	&.reservation {
		grid-template-columns: 1fr 2fr;
		grid-template-areas:
			'caution info'
			'penalty penalty'
			'shortcut shortcut';
		.caution {
			grid-area: caution;
		}
		.info {
			grid-area: info;
		}
		.penalty {
			grid-area: penalty;
		}
		.shortcut {
			grid-area: shortcut;
			.designed-border-1 {
				display: block;
				text-align: center;
				.border-inner {
					padding: 6rem 2rem;
				}
			}
		}
	}
	// ============================== 예약하기  끝  ==============================

	// ============================== 주변소개 시작 ==============================
	&.around {
		grid-template-columns: repeat(3, 1fr);
		button {
			text-align: left;
			figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(1);
			}
			.text {
				margin: 0.4rem 0;
				height: 12rem;
				@include text-ellipsis(6);
			}
			.source {
				color: #666;
				text-align: right;
				position: relative;
				font-size: 1.4rem;
			}
		}
		@include tablet {
			button .text {
				height: 8rem;
				-webkit-line-clamp: 4;
			}
		}
	}
	// ============================== 주변소개  끝  ==============================

	// ============================== 자주하는질문 시작 ==============================
	&.faq {
		grid-template-areas: 'sort faq-list faq-list';
		.sort {
			grid-area: sort;
			.tab-title {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				gap: 0.8rem;
				margin-top: 2rem;
				button {
					color: #999;
					transition-duration: 0.5s;
					font-size: 2rem;
					height: 2.8rem;
					&.active {
						color: #000;
						font-size: 2.4rem;
					}
					h3 {
						color: inherit;
						font-size: inherit;
					}
				}
			}
		}
		.faq-list {
			grid-area: faq-list;
			.title {
				display: flex;
				justify-content: space-between;
				align-items: flex-end;
				text-transform: capitalize;
				.btns {
					gap: 0.8rem;
					button {
						font-family: 'ChosunSm', serif;
						color: #666;
						&:last-child {
							margin-left: 0.8rem;
						}
					}
				}
			}
		}
		@include mobile {
			.sort {
				.tab-title {
					flex-direction: row;
					button {
						flex: 1;
					}
				}
			}
		}
	}
	// ============================== 자주하는질문  끝  ==============================
}
