@import './_mixin.scss';
@import './_reset.scss';
@import './_svg.scss';
@import './_variables.scss';
@import './_swiper.scss';

// ============================== Common 시작 ==============================

// 로고
i.logo {
	display: block;
	@include icon(logo);
}
// 폰트
h2,
h3,
h4,
.h2,
.h3,
.h4 {
	font-family: 'ChosunSm', serif;
}
h2,
.h2 {
	font-size: 3.2rem;
}
h3,
.h3 {
	font-size: 2.4rem;
}
h4,
.h4 {
	font-size: 2rem;
}
.text {
	word-break: break-all;
	text-align: justify;
	text-indent: 1rem;
	line-height: 130%;
	& + .text {
		margin-top: 0.2rem;
	}
}
b {
	font-family: 'ChosunKg', sans-serif;
}

// 아이콘
.icon-wifi {
	@include icon(icon-wifi);
}

// 스크롤바
body {
	@include scrollbar;
}
.modal-content {
	@include scrollbar(0.8rem, 0);
}
.table-wrap {
	@include scrollbar(0.6rem, 0);
}

// 피규어(이미지, 캡션)
figure {
	display: flex;
	flex-direction: column;
	gap: 0.4rem;
	img {
		object-fit: cover;
		aspect-ratio: 16/9;
	}
	figcaption {
		@include pseudo-icon(icon-up-fill);
		@include text-ellipsis(1);
		font-size: 1.4rem;
	}
	&.auto-height-figure {
		height: 100%;
		img {
			height: 1px;
			flex-grow: 1;
		}
	}
	@include mobile {
		&.auto-height-figure {
			img {
				height: auto;
			}
		}
	}
}

// 높이조절 요소
.auto-height {
	display: flex;
	flex-direction: column;
	height: calc(100% - 50px);
	gap: 2rem;
	&-component {
		height: 1px;
		flex-grow: 1;
	}
	&.info {
		gap: 2.4rem;
		height: 100%;
	}
}

// 리스트 스타일
[class^='list-style'] {
	li {
		position: relative;
		padding-left: 0.8rem;
		margin-top: 0.4rem;
		&:before {
			display: block;
			position: absolute;
			left: 0;
		}
	}
	&.list-style-bul {
		li {
			&::before {
				content: '';
				width: 0.3rem;
				height: 0.3rem;
				border-radius: 50%;
				background-color: $default;
				top: 0.8rem;
			}
			&.point {
				&::before {
					background-color: #f2f219;
				}
			}
		}
	}
	&.list-style-dash {
		li {
			&::before {
				content: '-';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
	&.list-style-star {
		li {
			&::before {
				content: '*';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
}

// 디자인된 보더
[class*='designed-border'] {
	position: relative;
	display: block;
	.border-top,
	.border-bottom {
		&::before,
		&::after {
			content: '';
			position: absolute;
			clip-path: polygon(0% 0%, 100% 0%, 100%100%);
			border-radius: 0.4rem;
		}
	}
	.border-top::before {
		left: 0;
		top: 0;
		transform: rotate(-90deg);
	}
	.border-top::after {
		right: 0;
		top: 0;
	}
	.border-bottom::before {
		left: 0;
		bottom: 0;
		transform: rotate(180deg);
	}
	.border-bottom::after {
		right: 0;
		bottom: 0;
		transform: rotate(90deg);
	}
	.border-inner {
		transition-duration: 0.5s;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
	}
	&.designed-border-1 {
		min-height: 9.6rem;
		.border-inner {
			clip-path: polygon(5.2rem 0%, calc(100% - 5.2rem) 0%, 100% 5.2rem, 100% calc(100% - 5.2rem), calc(100% - 5.2rem) 100%, 5.2rem 100%, 0% calc(100% - 5.2rem), 0% 5.2rem);
			border: 0.6rem double $default;
		}
		.border-top,
		.border-bottom {
			&::before,
			&::after {
				@include icon(border-1, 4.8rem);
			}
		}
	}
	&.designed-border-2 {
		min-height: 6.4rem;
		.border-inner {
			clip-path: polygon(3.4rem 0%, calc(100% - 3.4rem) 0%, 100% 3.4rem, 100% calc(100% - 3.4rem), calc(100% - 3.4rem) 100%, 3.4rem 100%, 0% calc(100% - 3.4rem), 0% 3.4rem);
			border: 0.6rem double $default;
		}
		.border-top,
		.border-bottom {
			&::before,
			&::after {
				@include icon(border-2, 3.2rem);
			}
		}
	}
}

// 메뉴버튼
.btn-menu {
	position: relative;
	width: 3.6rem;
	height: 3.6rem;
	span {
		display: block;
		width: 100%;
		height: 0.2rem;
		position: absolute;
		left: 0;
		top: calc(50% - 0.1rem);
		background: $default;
		transition-duration: 0.5s;
	}
	&::before,
	&::after {
		content: '';
		display: block;
		width: 50%;
		height: 0.2rem;
		position: absolute;
		background: $default;
		transition-duration: 0.5s;
	}
	&::before {
		left: 0;
		top: calc(25% - 0.1rem);
	}
	&::after {
		right: 0;
		bottom: calc(25% - 0.1rem);
	}
	&.active {
		span {
			transform: rotate(-45deg);
		}
		&::before {
			transform: rotate(45deg);
			left: 0.3rem;
			top: calc(25% + 0.2rem);
		}
		&::after {
			transform: rotate(45deg);
			right: 0.3rem;
			bottom: calc(25% + 0.2rem);
		}
	}
}
// ============================== Common  끝  ==============================

// ============================== 타이틀 시작 ==============================
.title {
	.pattern {
		position: relative;
		display: flex;
		justify-content: space-between;
		i.title-pattern {
			@include icon(title-pattern);
			position: absolute;
			left: calc(50% - 20rem);
			bottom: 0;
			width: 40rem;
			height: 9.8rem;
		}
		p {
			@include text-ellipsis(1);
			text-overflow: clip;
			flex-basis: calc(50% - 21rem);
			text-align: center;
			font-family: 'ChosunKm', serif;
			font-size: 2rem;
			letter-spacing: 0.3rem;
		}
		@include uxga {
			i.title-pattern {
				left: calc(50% - 16rem);
				width: 32rem;
				height: 7.8rem;
			}
			p {
				flex-basis: calc(50% - 17rem);
			}
		}
		@include tablet {
			i.title-pattern {
				left: calc(50% - 14rem);
				width: 28rem;
				height: 6.9rem;
			}
			p {
				flex-basis: calc(50% - 15rem);
				font-size: 1.8rem;
				letter-spacing: 0;
			}
		}
		@include mobile {
			i.title-pattern {
				left: calc(50% - 8rem);
				width: 16rem;
				height: 4rem;
			}
			p {
				flex-basis: calc(50% - 8.4rem);
				font-size: 1.2rem;
			}
		}
	}
	&-inner {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		border-top: 0.2rem solid $default;
		border-bottom: 0.6rem double $default;
		padding: 2rem 0;
		margin-top: 1rem;
		.h1 {
			font-family: 'ChosunKm', serif;
			text-align: center;
			font-size: 8rem;
			text-transform: uppercase;
			flex: 1;
		}
		.home,
		.date {
			display: flex;
			justify-content: center;
			align-items: center;
			font-family: 'ChosunKg', sans-serif;
			width: 24rem;
			border: 0.6rem double $default;
		}
		.home {
			gap: 1.2rem;
			.logo {
				width: 4rem;
				height: 4rem;
			}
		}
		.date {
			flex-direction: column;
			.month {
				text-align: center;
				font-size: 2rem;
			}
			.year {
				text-align: center;
				font-size: 3.6rem;
			}
			.btn-menu {
				width: 3.2rem;
				height: 3.2rem;
				display: none;
			}
		}
		@include tablet {
			.h1 {
				font-size: 6rem;
			}
			.home,
			.date {
				width: 7.2rem;
				border: none;
			}
			.home {
				.logo {
					width: 5.2rem;
					height: 5.2rem;
				}
				> div {
					display: none;
				}
			}
			.date {
				.year {
					font-size: 2.8rem;
				}
			}
		}
		@include mobile {
			align-items: center;
			padding: 1.2rem 0;
			.h1 {
				font-size: 2.6rem;
				line-height: 3.2rem;
			}
			.home,
			.date {
				aspect-ratio: 1/1;
				font-size: 0;
				width: 3.2rem;
			}
			.home {
				border: none;
			}
			.date {
				border: none;
				.month,
				.year {
					display: none;
				}
				.btn-menu {
					display: block;
				}
			}
		}
	}
}
// ============================== 타이틀  끝  ==============================

// ============================== 헤더 시작 ==============================
header {
	position: fixed;
	left: 0;
	top: -6rem;
	width: 100%;
	background: #f3f3f3;
	z-index: 99;
	box-shadow: 0 4px 4px rgba($default, 0.5);
	transition-duration: 0.5s;
	.header-inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 6rem;
		width: 160rem;
		margin: 0 auto;
		.home {
			.logo {
				width: 3.6rem;
				height: 3.6rem;
			}
		}
		.h2 {
			text-transform: uppercase;
			font-family: 'ChosunKm', serif;
		}
	}
	&::after {
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) 0 0/ 100% auto repeat-y;
	}
	&.active {
		top: 0;
	}
	.menu {
		height: 0;
		overflow: hidden;
		transition-duration: 0.5s;
		&.active {
			height: 4rem;
		}
		.gnb ul {
			border-bottom: none;
		}
	}
	@include uxga {
		.header-inner {
			width: 100%;
			padding: 0 2.4rem;
		}
	}
	@include mobile {
		.header-inner {
			.home {
				.logo {
					width: 3.2rem;
					height: 3.2rem;
				}
			}
			.h2 {
				font-size: 2.4rem;
			}
			.btn-menu {
				width: 3.2rem;
				height: 3.2rem;
			}
		}
		.menu {
			.gnb ul li {
				flex-basis: 33.33%;
			}
			&.active {
				height: 6.4rem;
			}
		}
	}
}
// ============================== 헤더  끝  ==============================

// ============================== 푸터 시작 ==============================
.footer {
	display: flex;
	gap: 2.4rem;
	margin-top: 2.4rem;
	padding-top: 1.6rem;
	border-top: 0.6rem double $default;
	.footer-logo {
		flex: 1;
		flex-basis: 300px;
		border: 0.6rem double $default;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 1rem;
		padding: 2rem 0;
		.logo {
			width: 3.6rem;
			height: 3.6rem;
		}
		div {
			p {
				font-family: 'ChosunSm', serif;
			}
		}
	}
	.footer-info {
		flex: 3;
		flex-basis: 90rem;
		position: relative;
		border: 0.2rem solid $default;
		text-align: center;
		padding: 2rem 0;
		.designed-border-2 {
			position: absolute;
			left: 2rem;
			top: 2rem;
			width: 120px;
			height: calc(100% - 4rem);
		}
	}
	.designed-border-1 {
		flex: 1;
		flex-basis: 30rem;
		text-align: center;
	}
	@include tablet {
		.footer-logo {
			flex-direction: column;
		}
		.footer-info {
			padding: 0;
			border: 0;
			.wrap {
				border: 0.2rem solid $default;
				margin-top: 1.2rem;
				padding: 1.2rem 0;
			}
			.designed-border-2 {
				position: relative;
				inset: unset;
				width: 100%;
				height: 6.4rem;
			}
		}
	}
	@include mobile {
		display: block;
		margin-top: 2.4rem;
		padding-top: 1.6rem;
		.footer-logo {
			flex-direction: row;
			border: none;
			padding: 0;
		}
		.footer-info {
			padding: 0;
			.designed-border-2 {
				margin-top: 0.8rem;
			}
			.wrap {
				border: none;
				margin-top: 0;
			}
		}
		.designed-border-1 {
			margin: 0.8rem auto 0;
			.border-inner {
				line-height: 9.6rem;
			}
		}
	}
}
.footer-copy {
	margin-top: 2.4rem;
	border-top: 0.2rem solid $default;
	text-align: center;
	padding: 2rem 0;
	@include tablet {
		margin-top: 1.6rem;
	}
}
// ============================== 푸터  끝  ==============================

// ============================== 내비게이션바 시작 ==============================
.gnb {
	ul {
		overflow: hidden;
		display: flex;
		justify-content: space-around;
		border-bottom: 0.2rem solid $default;
		font-family: 'ChosunSm', serif;
		text-align: center;
		transition-duration: 0.5s;
		flex-wrap: wrap;
		> li {
			position: relative;
			flex: 1;
			flex-basis: 16.66%;
			a {
				display: block;
				height: 4rem;
				position: relative;
				text-transform: uppercase;
				font-size: 1.8rem;
				span {
					display: block;
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					transition-duration: 0.5s;
					&.ko {
						opacity: 0;
					}
				}
				&.active {
					font-family: 'ChosunKm', serif;
					.en {
						opacity: 0;
					}
					.ko {
						opacity: 1;
					}
				}
				@include hoverable {
					&:hover {
						font-family: 'ChosunKm', serif;
						.en {
							opacity: 0;
						}
						.ko {
							opacity: 1;
						}
					}
				}
			}
		}
	}
	@include mobile {
		&:not(header .gnb) {
			overflow: hidden;
			ul {
				height: 0;
				transition-duration: 0.5s;
				border-bottom-color: transparent;
				li {
					flex-basis: 33.33%;
				}
			}
			&.active {
				ul {
					border-bottom-color: $default;
				}
			}
		}
		&.active ul {
			height: 6.4rem !important;
		}
		ul li a {
			font-size: 1.6rem;
			height: 3.2rem;
			letter-spacing: -0.1rem;
		}
	}
}
// ============================== 내비게이션바  끝  ==============================

// ============================== 루트 시작 ==============================
#root {
	position: relative;
	background: #e5e5e5;
	width: 100%;
	&::after {
		opacity: 0.8;
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) center top/ 100% auto repeat-y;
	}
	#wrap {
		max-width: 160rem;
		min-height: 100vh;
		padding: 9.8rem 0 0;
		margin: 0 auto;
		.content {
			display: grid;
			grid-template-columns: 1fr 3fr 1fr;
			gap: 2.4rem;
			padding-top: 4rem;
			> * {
				.title {
					margin-bottom: 1.2rem;
					&.with-viewmore {
						position: relative;
						display: flex;
						justify-content: space-between;
						align-items: center;
						a {
							color: #666;
						}
					}
				}
				.sub-title {
					margin-bottom: 1.2rem;
				}
			}
			> *:not([class*='-figure'], .rooms-text, .gallery),
			.info-reservation {
				padding-top: 1.6rem;
				border-top: 0.2rem solid $default;
			}
			@include tablet {
				padding-top: 3rem;
			}
			@include mobile {
				display: block;
				padding-top: 2.2rem;
				> *:not([class*='-figure'], .rooms-text, .gallery, :first-child),
				.info-reservation {
					margin-top: 2.4rem;
					padding-top: 1.2rem;
				}
			}
		}
		@include uxga {
			padding: 7.8rem 2.4rem 0;
		}
		@include tablet {
			padding-top: 6.9rem;
			.content {
				grid-template-columns: 1fr 2fr 1fr;
			}
		}
		@include mobile {
			padding: 3.9rem 1.6rem 0;
		}
	}
}

// ============================== 루트  끝  ==============================

.table-wrap {
	overflow: auto;
}
table {
	border-top: 0.6rem double $default;
	tbody,
	thead {
		tr {
			th,
			td {
				border-bottom: 0.1rem solid #666;
				padding: 1.2rem 0.8rem;
			}
			th {
				font-family: 'ChosunSm', serif;
			}
		}
		&.alC,
		.alC {
			text-align: center;
		}
	}
}

.accord-wrap {
	display: flex;
	flex-direction: column;
	gap: 0.2rem;
	border-top: 1px solid $default;
	padding-top: 2px;
	.accord-panel {
		border-top: 0.1rem solid transparent;
		border-bottom: 1px solid $default;
		overflow: hidden;
		transition-duration: 0.5s;
		.accord-title {
			display: block;
			width: 100%;
			text-align: left;
			padding: 1rem 4.4rem 1.2rem 1.2rem;
			position: relative;
			&::after,
			&::before {
				content: '';
				display: block;
				position: absolute;
				background-color: $default;
				border-radius: 1px;
			}
			&::before {
				width: 0.2rem;
				height: 2rem;
				right: 2.1rem;
				top: calc(50% - 1.1rem);
				transition-duration: 0.5s;
				opacity: 1;
			}
			&::after {
				width: 2rem;
				height: 0.2rem;
				right: 1.2rem;
				top: calc(50% - 0.2rem);
			}
		}
		.accord-content {
			max-height: 0;
			background-color: #bbb;
			transition-duration: 0.5s;
			transition-timing-function: ease-in-out;
			overflow: hidden;
			p {
				padding: 2.4rem;
			}
		}
		&.active {
			border-color: $default;
			.accord-title {
				&::before {
					transform: rotate(-90deg);
				}
			}
			.accord-content {
				max-height: 50rem;
			}
		}
	}
}
